import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-quantity-changer',
  templateUrl: './quantity-changer.component.html',
  styleUrls: ['./quantity-changer.component.scss'],
})
export class QuantityChangerComponent implements OnInit {

  @Input() quantity:number

  @Output() counterIncremented = new EventEmitter<number>();
  @Output() counterDecremented = new EventEmitter<number>();
  @Output() counterChanged = new EventEmitter<number>();
  constructor() { }

  decrementCounter(){
    if(this.quantity - 1 > 0){
      this.quantity = this.quantity - 1; 
      this.counterDecremented.emit(this.quantity);
    }
    //this.counterChanged.emit(this.quantity);
  }
  incrementCounter(){
    this.quantity = this.quantity + 1; 
    this.counterIncremented.emit(this.quantity);
    //this.counterChanged.emit(this.quantity);
  }

  counterChangedText(event){
    if( !isNaN(parseInt(event.detail.value)) && parseInt(event.detail.value) > 0 ){
      this.quantity = parseInt(event.detail.value);
      //console.log(event);
      this.counterChanged.emit(this.quantity);
    }
    else{
      this.quantity = 0;
    }
  }

  blurHandler(event){
    debugger;
    console.log(parseInt(event.detail.value));    
    if(isNaN(parseInt(event.detail.value))){
      
    }
  }
  
  ngOnInit() {}

}
