import { Component, OnInit } from '@angular/core';
import { ProductService } from 'src/app/shared/services/product.service';
import { ProductWithVariants } from 'src/app/shared/models/product.model';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss'],
})
export class ProductListComponent implements OnInit {

  constructor(public productService: ProductService) { }

  ngOnInit() {}

  filterItemsOfCategory(product : ProductWithVariants , category: string) {
    if (typeof product.variants !== 'undefined') {
      return product.variants.filter(x => x.variant_catagory == category);
    }
}

}
