import { CheckoutPage } from '../pages/checkout/checkout.page';
import { CartPage } from '../pages/cart/cart.page';
import { ProductDetailPage } from '../pages/product-detail/product-detail.page';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { MyOrdersComponent } from './my-orders/my-orders.component';
import { ViewOrderComponent } from './view-order/view-order.component';
import { ProfileComponent } from './profile/profile.component';
import { OrderFeedbackComponent } from './order-feedback/order-feedback.component';
import { WalletComponent } from './wallet/wallet.component';
import { OrderConfirmedComponent } from './pages/order-confirmed/order-confirmed.component';
import { ProductListComponent } from '../pages/product-list/product-list.component';
import { HomePage } from '../pages/home/home.page';
import { LocationComponent } from '../pages/location/location.component';

const routes: Routes = [
  { path: '', component: HomePage },
  { path: 'view-order', component: ViewOrderComponent },
  { path: 'my-orders', component: MyOrdersComponent },
  { path: 'profile', component: ProfileComponent },
  { path: 'order-feedback', component: OrderFeedbackComponent },
  { path: 'wallet', component: WalletComponent },
  { path: 'order-confirmed', component: OrderConfirmedComponent },
  { path: 'product-list', component: ProductListComponent },
  { path: 'product-detail/:productId', component: ProductDetailPage },
  { path: 'cart', component: CartPage },
  { path: 'checkout', component: CheckoutPage },
  { path: 'location', component: LocationComponent },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ContractorRoutingModule {}
