import { Product } from 'src/app/shared/models/product';
import { CartService } from './../../../shared/services/cart.service';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-cart-price-summary',
  templateUrl: './cart-price-summary.component.html',
  styleUrls: ['./cart-price-summary.component.scss'],
})
export class CartPriceSummaryComponent implements OnInit {

  @Input() products: Product[];

  @Output() btnRFQClick = new EventEmitter<number>();

  totalCartPrice: number = 0; 
  totalDeliveryCharges: number;
  walletBalance: number;
  couponValue: number = 0; 
  orderTotal: number;
  constructor(
    private _cartService:CartService
  ) { 
    console.log("constructor called");
  }

  ngOnInit() {
    console.log("ngOnInit called");
    console.log(this.products);
    this.calaculateAllTotals();
  }

  updateProducts(products: Product[]){
    this.products = products;
    console.log(products);
    this.calaculateAllTotals();
  }

  calculateTotalCartPrice(){
    this.totalCartPrice = 0;
    this.products.forEach(product => {this.totalCartPrice = this.totalCartPrice + (product.price*product.quantity)});
  }

  calculatetotalDeliveryCharges(){
    this.totalDeliveryCharges = 0; 
    this.products.forEach(product => {this.totalDeliveryCharges = this.totalDeliveryCharges + 10000});
  }

  calculateOrderTotal(){
    this.orderTotal = 0; 
    this.products.forEach(product => {this.orderTotal = this.orderTotal + (product.price*product.quantity)+10000});
    this.orderTotal = this.orderTotal - this.couponValue;
  }

  calaculateAllTotals(){
    this.calculateTotalCartPrice();
    this.calculatetotalDeliveryCharges();
    this.calculateOrderTotal();    
  } 

  btnRFQClickHandler(){
    this.btnRFQClick.emit(1);
  }

}
