import { CartService } from './../../shared/services/cart.service';
import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { Product } from 'src/app/shared/models/product';
import { CartPriceSummaryComponent } from './cart-price-summary/cart-price-summary.component';
import { AlertController } from '@ionic/angular';


@Component({
  selector: 'app-cart',
  templateUrl: './cart.page.html',
  styleUrls: ['./cart.page.scss'],
})
export class CartPage implements OnInit,AfterViewInit {

  
  /*products: Product[] = [
    {
      id:1,
      productName:"Pokemon",
      productCategory:"Category",
      price:50000,
      quantity:4
    },
    {
      id:2,
      productName:"The Legend of Zelda",
      productCategory:"Category",
      price:50000,
      quantity:4
    },
    {
      id:3,
      productName:"Super Mario World",
      productCategory:"Category",
      price:50000,
      quantity:4
    }
  ];*/

  @ViewChild('cartPriceSummary', { read: CartPriceSummaryComponent }) cartPriceSummary: CartPriceSummaryComponent;

  products: Product[];
  

  constructor(private _cartService:CartService, public alertController: AlertController) {
    this._cartService.getItems().then(items => {this.products = items});
   }
  ngAfterViewInit(): void {
    console.log(this.cartPriceSummary);
  }

  async presentAlert() {
    const alert = await this.alertController.create({
      header: 'Are You sure you wan to bulk RFQ?',
      message: 'Your Cart will be blocked once you click on OK(You wont be able to modify the cart)',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Okay',
          handler: () => {
            console.log('Confirm Okay');
          }
        }
      ]
    });

    await alert.present();
  }

  itemRemovedHandler(product){
    this._cartService.deleteItemFromCart(product.id).then(items => {
      this.products = items;
      this.cartPriceSummary.updateProducts(this.products);
    });
    
  }

  counterChangedHandler(product){
    var index = this.products.findIndex(objproduct => objproduct.id == product.id);
    var selectedProduct: Product = this.products[index];
    selectedProduct.quantity = product.quantity;
    this._cartService.updateItemInCart(selectedProduct.id, selectedProduct).then(items => {
      this.products = items;
      this.cartPriceSummary.updateProducts(this.products);
    });
    
  }

  btnEmptyCartClick(){
    this._cartService.clearCart().then(items => {
      this.products = items;
    });
    this.cartPriceSummary.updateProducts(this.products);
  }

  ngOnInit() {
  }

}
