import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-order-item-status',
  templateUrl: './order-item-status.component.html',
  styleUrls: ['./order-item-status.component.scss'],
})
export class OrderItemStatusComponent implements OnInit {
  @Input() status: string;
  statusList = [
    'Confirmed',
    'Packed',
    'Out for delivery',
    'Delivered',
  ];
  iconClasses = [
    'confirmed',
    'packed',
    'out-for-delivery',
    'delivered',
  ];
  statusIndex = 0;
  progressBarValue = 0;
  ngOnInit() {
    console.log(this.status);
    this.statusIndex = this.statusList.indexOf(this.status);
    this.progressBarValue = ((this.statusIndex * 2) + 1) / ((this.statusList.length - 1) * 2);
  }
  getClass(i: number): string {
    return `icon-wrapper ${this.iconClasses[i]} ${this.statusIndex < i ? '' : 'complete'}`;
  }
}
