import { Component, OnDestroy, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { PopoverComponent } from '../popover/popover.component';
import { UserService } from 'src/app/shared/services/user.service';
import { Router } from '@angular/router';
import { SearchService } from 'src/app/shared/services/search.service';

@Component({
  selector: 'app-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.scss'],
})
export class ToolbarComponent implements OnInit, OnDestroy {
  popoverComponent: any;
  isItemAvailable = false;
  public items: any;
  constructor(
    public popoverController: PopoverController,
    private searchService: SearchService,
    private user: UserService,
    private router: Router
  ) {}
  ngOnInit(): void {
    this.searchService.initializeItems();
    this.user.closePopover.subscribe(() => {
      if (this.popoverComponent) {
        this.popoverComponent.dismiss();
      }
    });
  }
  route(url: string) {
    this.router.navigateByUrl(url);
    this.user.closePopover.emit();
  }
  async presentPopover(event: any) {
    this.popoverComponent = await this.popoverController.create({
      component: PopoverComponent,
      event,
      showBackdrop: false,
    });
    return await this.popoverComponent.present();
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.searchService.initializeItems();

    // set val to the value of the searchbar
    const val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.isItemAvailable = true;
      this.items = this.searchService.filterItems(val);
    } else {
      this.isItemAvailable = false;
    }
  }
  ngOnDestroy() {
    console.log('destroyed');
  }
}
