import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../../services/user.service';
import { PopoverController } from '@ionic/angular';
import { PopoverComponent } from '../popover/popover.component';

@Component({
  selector: 'app-toolbar-wrapper',
  templateUrl: './toolbar-wrapper.component.html',
  styleUrls: ['./toolbar-wrapper.component.scss'],
})
export class ToolbarWrapperComponent implements OnInit {
  isSearchActive = false;
  navigationItems = [
    {
      category: 'Building materials',
      subcategories: ['Cement', 'TMT Steel bars', 'Bricks', 'Dust and sand', 'RMC', 'Waterproofing'],
    },
    {
      category: 'Plumbing',
      subcategories: ['CPVC Pipe', 'uPVC Pipe', 'Water Tanks', 'CPVC Fittings', 'UPVC Fittings'],
    },
    {
      category: 'Electricals',
      subcategories: ['Electrical fittings', 'WIres', 'Fans', 'Lights', 'Cover plates', 'Switches and sockets'],
    },
    {
      category: 'Sanitaryware',
      subcategories: ['Bath and shower', 'Faucets', 'Toilet', 'Wash basins', 'Accessories', 'Kitchen'],
    },
    {
      category: 'Tiles',
      subcategories: ['Paints', 'Putty', 'POP', 'White cement'],
    },
  ];
  menuOptions = [
    {
      label: 'Home',
      icon: 'home',
      route: '',
    },
    {
      label: 'Profile',
      icon: 'person-circle',
      route: 'profile',
    },
    {
      label: 'Orders',
      icon: 'cube',
      route: 'my-orders',
    },
    {
      label: 'Wallet',
      icon: 'wallet',
      route: 'wallet',
    },
    {
      label: 'FAQ',
      icon: 'help-circle',
      route: '',
    },
    {
      label: 'T&C',
      icon: 'document-text',
      route: '',
    },
    {
      label: 'Help Center',
      icon: 'call',
      route: '',
    },
    {
      label: 'About us',
      icon: 'information-circle',
      route: '',
    },
    {
      label: 'Logout',
      icon: 'log-out',
      route: 'logout',
    },
  ];
  popoverComponent: any;
  @Input() heading: string;
  constructor(private router: Router, private user: UserService, public popoverController: PopoverController) {}
  ngOnInit() {
    this.user.closePopover.subscribe(() => {
      if (this.popoverComponent) {
        this.popoverComponent.dismiss();
      }
    });
  }
  navigateTo(route: string): void {
    this.router.navigateByUrl(route);
  }
  async presentPopover(event: any) {
    this.popoverComponent = await this.popoverController.create({
      component: PopoverComponent,
      event,
      showBackdrop: false,
    });
    return await this.popoverComponent.present();
  }

  redirectToLocation(){
    this.router.navigateByUrl('/location');
  }
}
