import { Component, OnInit, Input, Output,EventEmitter } from '@angular/core';
import { Product } from 'src/app/shared/models/product';


@Component({
  selector: 'app-cart-item',
  templateUrl: './cart-item.component.html',
  styleUrls: ['./cart-item.component.scss'],
})
export class CartItemComponent implements OnInit {

  @Input() product: Product;
  @Output() itemRemoved: EventEmitter<Product> = new EventEmitter<Product>();
  @Output() counterChanged: EventEmitter<Product> = new EventEmitter<Product>();
  constructor() { }

  ngOnInit() {}

  btnCloseClick(product: Product){
    this.itemRemoved.emit(this.product);
  }

  counterChangedHandler(event){
    console.log(event);
    this.product.quantity = event;
    this.counterChanged.emit(this.product);
  }

}
