import { HttpService } from './../../shared/services/http.service';
import { CartService } from './../../shared/services/cart.service';
import { ProductService } from './../../shared/services/product.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { IonSlides, IonSegment } from '@ionic/angular';
import { Product,Variant } from 'src/app/shared/models/product';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-product-detail',
  templateUrl: './product-detail.page.html',
  styleUrls: ['./product-detail.page.scss'],
})
export class ProductDetailPage implements OnInit {

  product:Product;
  productId:number
  headerVariant: Variant;
  totalCost:number;
  constructor(
    private _productService: ProductService,
    private _cartService:CartService,
    private _httpService: HttpService,
    private _router:Router,
    private _route: ActivatedRoute) {
      this._route.paramMap.subscribe(params => {
        this.productId = +params.get('productId');
        this._productService.getProduct().subscribe((products) => {
          //for(let product of products.products){
          //console.log(products);
          for(let product of products){
            if(product.id == this.productId){
              this.product=product; 
              //console.log(this.product);
              this.headerVariant = product.variants[0];
              this.totalCost = this.product.price*this.product.quantity;
            }
          }
          //console.log(products.products[0]);
        });
      });
      //this._httpService.get('locations.json').subscribe(data => {console.log(data)});
   }

  ngOnInit() {
    

  }

  logEvent(quantity: number){
    console.log(quantity);
    this.product.quantity = quantity;
    this.totalCost = this.product.quantity * this.product.price;
  }

  logEventInc($incEvent){
    console.log("logEventInc" +$incEvent);
  }

  logEventDec($event){
    console.log("logEventDec"+ $event);
  }

  @ViewChild('slides') slider: IonSlides;  
  @ViewChild('segmentComponent') segmentComponent: IonSegment;  
  segment = 0;  
  async segmentChanged(ev: any) {
    this.segment = ev.detail.value;
    await this.slider.slideTo(this.segment);  
  }  
  async slideChanged() {  
    this.segment = await this.slider.getActiveIndex();
    this.segmentComponent.value = this.segment.toString();
  }  

  addProductToCart(){
    this._cartService.addToCart(this.product);
    this._router.navigate(['/cart']);
  }

}
